import React from "react"
import {
    AboutWrapper,
    FirstAbout, FirstAboutTitle,
    FirstAboutWrapper, HeaderOne,
    Paragraph,
    SecondAbout, SecondParagraph,
} from "./home-about.style"
import Button from "../button/button.component";
import {graphql, useStaticQuery} from "gatsby";
import Img from "gatsby-image";
import {StyledDivider} from "../svg/svg.component";

const HomeAbout = () => {

    const data = useStaticQuery(graphql`
  query HomeAboutQuery {
  image: file(relativePath: {eq: "plane2.jpg"}) {
    id
    childImageSharp {
      fluid(maxWidth: 5000) {
      ...GatsbyImageSharpFluid
      }
    }
  }
}
 `)

    return (
        <AboutWrapper>
            <FirstAboutWrapper>
                <FirstAbout>
                    <FirstAboutTitle>We are Qube pcs</FirstAboutTitle>
                    <SecondParagraph> QUBE PCS are an internationally experienced professional services company that
                        support global
                        organisations that wish to invest in, own and operate assets. We’re focused on providing our
                        clients with a
                        level of service that is tailored to their specific needs, with the main goal of delivering
                        projects to a
                        high
                        cost and time effective outcome in a sustainable and safe environment.</SecondParagraph>
                    <Button link='/404' type='secondary'>Learn More</Button>
                </FirstAbout>
            </FirstAboutWrapper>

            <SecondAbout>
                <Img fluid={data.image.childImageSharp.fluid}/>
                <div style={{padding: '50px 5vw'}}>
                    <HeaderOne style={{textAlign: 'center'}}>Welcome</HeaderOne>
                    <StyledDivider/>
                    <Paragraph>
                        We work closely with clients, surveyors, architects, contractors and consultants throughout all
                        phases of design development and building processes to ensure projects are delivered with the
                        most effective
                        outcome
                    </Paragraph>
                </div>
            </SecondAbout>
        </AboutWrapper>
    )
}

export default HomeAbout
